/*-----Rating Stars-----*/

.rating-stars {
	width: 100%;
	text-align: center;
	.rating-stars-container {
		font-size: 0px;
		.rating-star {
			display: inline-block;
			font-size: 32px;
			cursor: pointer;
			padding: 5px 10px;
			color: #f4f4f4;
			&.sm {
				display: inline-block;
				font-size: 14px;
				color: $border;
				cursor: pointer;
				padding: 5px;
			}
			&.is--active,
			&.is--hover {
				color: $primary;
			}
			&.is--no-hover {
				color: #eaebf3;
			}
			&.is--active .fa-heart,
			&.is--hover .fa-heart {
				color: $primary;
			}
			.fa-heart .is--no-hover {
				color: #eaebf3;
			}
		}
	}
	input {
		display: block;
		margin: 0 auto;
		text-align: center;
		padding: 0.375rem 0.75rem;
		font-size: 0.9375rem !important;
		line-height: 1.6;
		color: #495057;
		background-color: $white;
		background-clip: padding-box;
		border: 1px solid $border;
		border-radius: 3px;
		transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
	}
}


/*-----End Rating Stars-----*/


/*-----Br Themes-----*/

.br-theme-bars-horizontal .br-widget {
	a {
		background-color: #f4f4f4;
		&.br-active,
		&.br-selected {
			background-color: $primary;
		}
	}
	.br-current-rating {
		color: $primary;
	}
}

.br-theme-bars-pill .br-widget a {
	background-color: #f4f4f4;
	color: $primary;
	&.br-active,
	&.br-selected {
		background-color: $primary;
		color: white;
	}
}

.br-theme-bars-square .br-widget a {
	&.br-active,
	&.br-selected {
		border: 2px solid #f4f4f4;
		color: $primary;
	}
	border: 2px solid $border;
	background-color: $white;
	color: #5d509c;
}

.br-theme-bars-movie .br-widget {
	a {
		&.br-active,
		&.br-selected {
			background-color: $primary;
		}
		background-color: #f4f4f4;
	}
	.br-current-rating {
		color: $primary;
	}
}

.br-theme-bars-1to10 .br-widget {
	a {
		background-color: #f4f4f4;
		&.br-active,
		&.br-selected {
			background-color: $primary;
		}
	}
	.br-current-rating {
		color: $primary;
	}
}

.br-theme-fontawesome-stars .br-widget a {
	&.br-active:after,
	&.br-selected:after {
		color: $primary;
	}
}

.br-theme-fontawesome-stars-o .br-widget a {
	&.br-active:after,
	&.br-selected:after {
		content: '\f005';
		color: $primary;
	}
	&.br-fractional:after {
		content: '\f123';
		color: $primary;
	}
}

.br-theme-fontawesome-stars .br-widget a:after {
	content: '\f005';
	color: #f4f4f4;
}

.br-theme-fontawesome-stars-o .br-widget a:after {
	content: '\f006';
	color: #f4f4f4;
}


/*-----Br Themes-----*/